.case {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: calc(10vw / $str-wid-m);
    font-size: calc(20vw / $str-wid-m);
    color: $gray;
    line-height: 1.5;
    margin: calc(30vw / $str-wid-m) auto;
    &__pop {
        &.hidden {
            display: none !important;
        }
        position: fixed;
        z-index: 99999;
        top: 0;
        left: 0;
        @include flex-col-center-center;
        width: 100%;
        height: 100vh;
        border-color: $blue;
        background-color: $blue--2;
        &__container {
            position: relative;
            width: 80%;
            height: auto;
            padding: calc(20vw / $str-wid-m);
            border: 2px solid $white;
            border-radius: 20px;
            .carousel {
                &.carousel-slider {
                    overflow: visible;
                    .control-arrow {
                        position: absolute;
                        top: 50%;
                        @include flex-col-center-center;
                        width: calc(50vw / $str-wid-m);
                        height: calc(50vw / $str-wid-m);
                        border-radius: 100%;
                        padding: 0;
                        background-color: $red;
                        transform: translate(80%, -50%);
                        &:hover {
                            background-color: $red;
                        }
                        &::after {
                            display: block;
                            content: "";
                            border: 0;
                            width: calc(8vw / $str-wid-m);
                            height: calc(1.5vw / $str-wid-m);
                            background-color: $white;
                            border-radius: 10px;
                            transform: rotate(135deg) translateY(-3vw / $str-wid-m);
                        }
                        &::before {
                            content: "";
                            display: block;
                            border: 0;
                            width: calc(8vw / $str-wid-m);
                            height: calc(1.5vw / $str-wid-m);
                            background-color: $white;
                            border-radius: 10px;
                            transform: rotate(45deg) translateY(-3vw / $str-wid-m);
                        }
                    }
                    .control-prev {
                        transform: translate(-80%, -50%);
                        &::after {
                            display: block;
                            content: "";
                            border: 0;
                            width: calc(8vw / $str-wid-m);
                            height: calc(1.5vw / $str-wid-m);
                            background-color: $white;
                            transform: rotate(315deg) translateY(-5vw / $str-wid-m);
                        }
                        &::before {
                            content: "";
                            display: block;
                            border: 0;
                            width: calc(8vw / $str-wid-m);
                            height: calc(1.5vw / $str-wid-m);
                            background-color: $white;
                            transform: rotate(225deg) translateY(-5vw / $str-wid-m);
                        }
                    }
                }
                .slide {
                    overflow: hidden;
                    padding-bottom: 7vw;
                    max-height: 90vw;
                    border: 1px solid $white !important;
                    border-radius: 10px;
                    width: 80%;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                    .legend {
                        width: 100%;
                        left: 0;
                        bottom: 0;
                        margin-left: 0;
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;
                        background-color: transparent;
                        color: $white;
                        font-size: calc(15vw / $str-wid-m);
                    }
                }
            }
        }
        .pop__close {
            cursor: pointer;
            position: absolute;
            top: calc(5vw / $str-wid-m);
            right: calc(5vw / $str-wid-m);
            @include flex-col-center-center;
            width: 20px;
            height: 20px;
            color: $white;
            border-radius: 100%;
            border: 1px solid $white;
        }
    }
    &__item {
        position: relative;
        display: inline-block;
        border-radius: 20px;
        border: 2px solid $gray;
        background-color: $white;
        opacity: 0.85;
        min-height: calc(200vw / $str-wid-m);
        padding: calc(10vw / $str-wid-m);
        padding-bottom: 0;
        .case__desc {
            display: inline-block;
            width: 80%;
            position: relative;
            padding: calc(20vw / $str-wid-m);
            padding-left: 0;
            padding-bottom: 0;
            font-size: calc(6vw / $str-wid-m);
            font-weight: 100;
            border-top: 2px solid $gray;
            border-right: 2px solid $gray;
            border-top-right-radius: 30px;
            background-color: $white;
            transform: translateY(-20%);
            span {
                top: calc(-10vw / $str-wid-m);
                font-size: calc(5vw / $str-wid-m);
                position: absolute;
                display: block;
                padding: calc(2vw / $str-wid-m) calc(5vw / $str-wid-m);
                border-radius: 20px;
                border: 1px solid $gray;
                background-color: $white;
            }
        }
    }
    &__img {
        cursor: pointer;
        @include flex-col-center-center;
        width: 100%;
        height: calc(180vw / $str-wid-m);
        overflow: hidden;
    }
    &__date {
        font-size: calc(10vw / $str-wid-m);
        position: absolute;
        display: inline-block;
        text-align: center;
        right: calc(10vw / $str-wid-m);
        margin-top: calc(10vw / $str-wid-m);
    }
}
@media (min-width: 750px) {
    .case {
        grid-template-columns: 1fr 1fr;
        margin: 80px 0 40px;
        font-size: 16px;
        &__pop {
            &__container {
                width: 50%;
                padding: 50px;
                .carousel {
                    &.carousel-slider {
                        .control-arrow {
                            top: 50%;
                            width: 64px;
                            height: 64px;
                            transform: translate(80%, -50%);
                            &::after {
                                width: 20px;
                                height: 3px;
                                transform: rotate(135deg) translateY(-7px);
                            }
                            &::before {
                                width: 20px;
                                height: 3px;
                                transform: rotate(45deg) translateY(-7px);
                            }
                        }
                        .control-prev {
                            transform: translate(-80%, -50%);
                            &::after {
                                width: 20px;
                                height: 3px;
                                transform: rotate(315deg) translateY(-12px);
                            }
                            &::before {
                                width: 20px;
                                height: 3px;
                                transform: rotate(225deg) translateY(-12px);
                            }
                        }
                    }

                    .slide {
                        max-height: 35vw;
                        padding-bottom: 50px;
                        .legend {
                            font-size: 22px;
                        }
                    }
                }
                .pop__close {
                    width: 30px;
                    height: 30px;
                    font-size: 15px;
                }
            }
        }
        &__item {
            padding: 20px;
            min-height: 300px;
            .case__desc {
                padding: 30px 15px 0 0;
                font-size: 16px;
                span {
                    font-size: 14px;
                    top: -20px;
                }
            }
        }
        &__img {
            height: 250px;
        }
        &__date {
            font-size: 16px;
            right: 20px;
            margin-top: 50px;
        }
    }
}
