.superman {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    @include flex-col-center-center;
    width: 100%;
    height: 100vh;
    transition: all 1s;
    overflow: hidden;
    background-color: $yellow;
    animation: fadeOut 1s 10s alternate linear forwards;
    img{
        width: 10%;
        animation: flyAway 10s alternate linear forwards;
    }
}

@media (min-width: 750px) {
}

@media (min-width: 1200px) {

}

@keyframes flyAway {
    0%{
        transform: none;
    }
    80%{
        transform: none;
    }
    100%{
        transform: translate(-1000px,50px);
    }
}
@keyframes fadeOut {
    0%{
        opacity: 1;
    }
    90%{
        opacity: 0;
        width: 100%;
        height: 100vh;
    }
    100%{
        opacity: 0;
        width: 0;
        height: 0;
    }
}